
import { defineComponent,watch,reactive,toRefs } from 'vue'
import { getJson } from '@/api/apiConfig'
import { gettopplaylist } from '@/api/api'
import LoadingCpn from "@/components/common/loadingcpn.vue"
import { useRouter } from 'vue-router'
import { goPage } from '@/utils/common'
interface page{
    pageSize: number,
    pageNum: number,
    total: number,
}
interface state{
    cat:string,
    songsheetData:any[],
    loading:boolean,
    pageObj:page
}
export default defineComponent({
    name:'songsheetlist',
    components:{
        LoadingCpn,
    },
    props:[
        'cat'
    ],
    setup (props) {
        let state = reactive<state>({
            cat: props.cat,
            songsheetData:[],
            loading:false,
            pageObj:{
                pageSize:100,
                pageNum:1,
                total:0,
            },
        })
        let getSongSheet = () => {
            let offset = state.pageObj.pageSize * (state.pageObj.pageNum - 1)
            state.loading = true
            getJson(gettopplaylist,{cat:state.cat,offset:offset,limit:state.pageObj.pageSize},(res:{code:number,playlists:any[],total:number}) => {
                state.loading = false
                if(res.code == 200){
                    state.songsheetData = res.playlists
                    state.pageObj.total = res.total
                }
            },(err:any) => {

            })
        }
        let handleCurrentChange = (pagenum:number) => {
            state.pageObj.pageNum = pagenum
            state.songsheetData = []
            getSongSheet()
        }
        watch(() => props.cat,(newValue) => {
            state.cat = newValue
            init()
        })
        let router = useRouter()
        let init = () => {
            state.songsheetData = []
            state.pageObj = {
                pageSize:100,
                pageNum:1,
                total:0,
            }
            getSongSheet()
        }
        init()
        return {
            ...toRefs(state),
            handleCurrentChange,
            router,
            goPage
        }
    }
})
